    // 
    tableRowClassName({row, rowIndex}) {
      if(row.level == '') return 'commonWarn'
      else if(row.level == '') return 'middleWarn'
      else if(row.level == '') return 'warnWarn'
      else if(row.level == '') return 'seriousWarn'
    },
    // 
    tableScroll() {
      const table = this.$refs.warnTable
      const divData = table.bodyWrapper
      if(this.tableData.length > 10) {
        this.tableInterval = setInterval(() => {
          divData.scrollTop += 1;
          if (parseInt(divData.clientHeight + divData.scrollTop) == divData.scrollHeight) {
            divData.scrollTop = 0;
          }
        }, 30);
      }
    },
    // 
    mouseEnter() { 
      clearInterval(this.tableInterval);
      this.tableInterval = null;
    },
    // 
    mouseLeave() {
      this.tableScroll()
    },
  }
