<template>
  <div id="assetChange">
    <div class="assetChange-title">
      <span>IP</span>
      <span></span>
    </div>
    <div class="assetChange-list">
      <ul id="assetChange-swiperWrap" style="height: calc(100%-40px)" @mousemove="mousemoveIn" @mouseleave="goSwiper">
        <li>
          <span>10.141.192.168</span>
          <span></span>
        </li>
        <li v-for="(item, index) in 10" :key="index">
          <span>10.141.192.168</span>
          <span></span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      swiperTime: null,
      cur: 0
    };
  },
  mounted() {
    this.goSwiper();
  },

  methods: {
    goSwiper() {
      const _this = this;
      const ele = document.getElementById('assetChange-swiperWrap'); // ul 
      const listDom = ele.getElementsByTagName('li');
      const list = [...listDom]; // li
      const len = list.length;

      if (len > 5) {
        // 5swiper
        // list.push(list[0]); // domdom
        const swpier = function (delay) {
          _this.swiperTime = setTimeout(function () {
            _this.cur += 1;
            // console.log(cur, len);
            // if (len - cur == 6) {
            if (_this.cur > len) {
              // 
              _this.cur = 0;
              // ele.style.transition = 'none';
              ele.style.transform = 'translateY(0)';
              ele.style.transition = 'all 1s';
              swpier(1000);
            } else {
              const top = -27.094 * _this.cur;
              ele.style.transform = 'translateY(' + top + 'px)';
              ele.style.transition = 'all 1s';
              _this.cur === len ? swpier(1000) : swpier(2000); // 2s
            }
          }, delay);
        };
        swpier(1000);
      }
    },
    mousemoveIn() {
      clearTimeout(this.swiperTime);
    }
  }
};
</script>

